@import url(https://fonts.googleapis.com/css?family=Montserrat:400);
.react-datepicker-popper {
  z-index: 99999;
}
.gl-del-btn i {
  font-weight: bold;
}

.gl-del-btn i:hover {
  color: white;
}

.waves-effect.waves-orange-custom .waves-ripple {
  background-color: rgba(255, 171, 145, .75);
}
.row .col.gl-header-footer {
  display: flex;
  justify-content: space-between;
  padding: 1rem 1rem 0 0;
}

.gl-header-footer .waves-effect.waves-purple-custom .waves-ripple  {
  background-color: rgba(69, 39, 160, .75);
}

.gl-units{
  font-size: 1.2rem;
}

.gl-units .select-wrapper {
  width: 45px;
}

.gl-header .gl-units .select-wrapper input.select-dropdown {
  margin-left: 0.5rem;
  text-align: center;
}

.gl-units .dropdown-content {
  min-width: 50px;
}
.gl-header .row {
  margin-bottom: 0.75rem;
}

.gl-header-input-label {
  margin-right: 0.5rem;
}

.gl-header .input-field {
  margin-top: 0;
  margin-bottom: 4px;
}

.gl-header .input-field.inline input {
  margin-bottom: 0;
}

.gl-header .input-field input:not(.browser-default) {
  width: 115px;
  height: 1.5rem;
  color: #000000;
  font-size: 1.2rem;
  border-bottom: none;
}

.gl-header .select-wrapper input.select-dropdown {
  height: 1.5rem;
  margin: 0;
  padding: 0.5rem 0 0.5rem 0.5rem;
  font-size: 1.2rem;
  border-bottom: none;
}

.gl-header  .select-wrapper .caret{
  display: none;
}

.gl-header-del-btn {
  display: flex;
  justify-content: flex-end;
}

.gl-header-description textarea.materialize-textarea {
  height: 2rem;
  min-height: 1.5rem;
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  color: rgb(0 , 0, 0);
  border-bottom: none;
}

.gl-header-title input[type=text]:not(.browser-default) {
  font-size: 1.6rem;
  border-bottom: none;
}

.gl-header .select-wrapper input.select-dropdown:focus {
  border-bottom: none;
  background-color: rgba(0,0,0,.05);
}

.gl-header-title input[type=text]:not(.browser-default):focus:not([readonly]),
.gl-header-description textarea.materialize-textarea:focus:not([readonly]) {
  border-bottom: none;
}

.gl-header-date input[type=text]:not(.browser-default):focus:not([readonly]) {
  border-bottom: none;
  box-shadow: none;
}

.row .col.gl-statistics {
  padding: 0.5rem 1rem;
  background-color:rgba(224, 247, 250, 0.1);
}

.gl-category-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  background: rgba(0, 0, 0, 0.05);
}

.gl-category-title {
  padding: 0 1rem 0.3px 1.5rem;
}

.collapsible-header-item {
  display: flex;
  align-items: center;
  font-size: 1.7rem;
}

.collapsible-header-item .collapsible-header {
  padding: 0;
  border: none;
  align-items: center;
}

.collapsible-header i {
  width: 3rem;
}

.expand.collapsible-header {
  margin-right: 0.3rem;
  background: none;
}

.material-icons.expand {
  margin-right: 0;
}

.gl-category-header .select-wrapper input.select-dropdown {
  margin: 0;
  padding-left: .75rem;
  font-size: 1.6rem;
  border-bottom: none;
  color: rgba(0, 0, 0, 0.85);
}

.gl-category-header .select-wrapper input.select-dropdown:focus {
  border-bottom: none;
  background-color: rgba(0,0,0,.05);
}

.gl-category-header .select-wrapper .caret {
  display: none;
}

.gl-category-item.row {
  display: flex;
  margin-bottom: 0;
}

.gl-category-item.row .col {
  display: flex;
  align-items: center;
}

.gl-category-item .first-col {
  padding: 0 0.75rem 0 0;
}

.gl-category-item .last-col {
  justify-content: flex-end;
  padding: 0;
}


.last-col [type="checkbox"] + span:not(.lever) {
  margin-top: 6px;
  padding-left: 26px;
}

.gl-category-item input[type=number]:not(.browser-default) {
  width: 44px;
  height: 1.5rem;
  margin: 0 4px 0 0;
  font-size: 1.1rem;
  text-align: center;
  border-bottom: none;
}

.gl-category-item input[type=number]:not(.browser-default):focus:not([readonly]) {
  box-shadow: 0 0 0 0.2rem #4db6ac;
  border-radius: 2px;
  border: none;
}

.gl-category-item .select-wrapper input.select-dropdown {
  height: 1.5rem;
  margin: 0;
  padding: 0.5rem 0 0.5rem 0.5rem;
  font-size: 1rem;
  border-bottom: none;
}

.gl-category-item .select-wrapper input.select-dropdown:focus {
  border-bottom: none;
  background-color: rgba(0,0,0,.05);
}

.gl-category-item  .select-wrapper .caret{
  display: none;
}

.btn-flat.gl-add-item-btn,
.btn-flat.gl-add-item-btn i {
  font-size: 1.2rem;
  font-weight: bold;
  color:#00695c;
}

.btn-flat.gl-add-item-btn:hover *,
.btn-flat.gl-add-item-btn:hover {
  color: white;
}

.gl-category-footer {
  padding: 1rem 0;
}

.gl-category.collapsible {
  margin: 0;
  border: none;
}

.gl-category .collapsible-body {
  padding: 2rem 0 2rem 1rem;
  border: none;
}
.btn-flat.gl-add-category-btn,
.btn-flat.gl-add-category-btn i {
  font-size: 1.2rem;
  font-weight: bold;
  color: #4527a0;
}

.btn-flat.gl-add-category-btn:hover *,
.btn-flat.gl-add-category-btn:hover {
  color: white;
}

ul li div.gl-nav-element {
  display: block;
  padding: 0 15px;
  font-size: 1rem;
  color: #fff;
  cursor: pointer;
  transition: background-color .3s;
}

nav ul div.gl-nav-element:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.sidenav li > div.sidenav-close {
  color: rgba(0, 0, 0, 0.87);
  display: block;
  font-size: 14px;
  font-weight: 500;
  height: 48px;
  line-height: 48px;
  padding: 0 32px;
  cursor: pointer;
}

.sidenav li > div.sidenav-close.bnt-logout {
  margin: 10px 35px;
}

.sidenav li > div.sidenav-close:hover {
  background-color: rgba(0, 0, 0, 0.05);
}

.sidenav li > div.sidenav-close > i,
.sidenav li > div.sidenav-close > i.material-icons {
  float: left;
  height: 48px;
  line-height: 48px;
  margin: 0 32px 0 0;
  width: 24px;
  color: rgba(0, 0, 0, 0.54);
}

.btn-sidenav-login {
  text-decoration: underline;
}
.card {
  min-width: 240px;
}

.card-image {
  min-height: 136px;
  background: #00695c;
}

.image-overlay {
  object-fit: cover;
  opacity: 0.55;
}
.gl-board-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.75rem 1rem 0.75rem;
}
/* ============ My Styles ============ */
.mb {
  margin-bottom: 1.5rem;
}

.mt {
  margin-top: 1.5rem;
}

.mr {
  margin-right: 10px;
}

.ml {
  margin-left: 10px;
}

.px {
  padding: 1rem 0;
}

.pl {
  padding-left: 0.75rem;
}

.pr {
  padding-right: 0.75rem;
}

.df {
  display: flex;
  align-items: center;
}

/* ========== Common ========== */

.semitransparent-container,
.row .col.semitransparent-container {
  margin-top:  5%;
  margin-bottom: 1.5rem;
  padding: 1.5rem;
  background-color:rgba(224, 247, 250, 0.1);
}

.error-message {
  font-size: 0.95rem;
  color: #F44336;
}

ul {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

.divider {
  margin-bottom: 1rem;
}

/* ============= inputs ============== */

::-webkit-input-placeholder { 
  color: #424242;
  opacity: 1;
}

:-ms-input-placeholder { 
  color: #424242;
  opacity: 1;
}

::-ms-input-placeholder { 
  color: #424242;
  opacity: 1;
}

::placeholder { 
  color: #424242;
  opacity: 1;
}

input,
input:not(.browser-default),
textarea.materialize-textarea {
  font-family: "Montserrat";
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    /* -webkit-box-shadow: 0 0 0 30px #bfd4d9 inset !important; */
    -webkit-box-shadow: 0 0 0 30px #a6b8bf inset !important;
}

.input-field input:not(.browser-default),
input:-internal-autofill-selected,
textarea {
  color: #424242;
}

.input-field label {
  color: #424242;
}

.input-field input:not(.browser-default),
textarea.materialize-textarea {
  border-bottom: 1px solid  #424242;
}

/* ========== buttons ========= */

/* .btn-width {
  min-width: 22%;
} */

/* .waves-effect.waves-orange-custom .waves-ripple {
  background-color: rgba(255, 171, 145, .75);
} */

.btn,
.btn-large,
.btn-small,
.collapsible {
  box-shadow: none;
}

.btn-small:hover {
  background-color: transparent;
}

.btn-flat.btn-flat[disabled],
.btn-flat.btn-flat[disabled] i {
  color: rgba(0, 0, 0, 0.25) !important;
}

.sidenav li > a.btn-small {
  margin: 10px 35px;
}


@media only screen and (max-width: 601px) {
  .semitransparent-container,
  .row .col.semitransparent-container {
    margin-top:  0.8rem;
    margin-bottom: 0.8rem;
    padding: 0.8rem 0.6rem 1.5rem 0.6rem;
  }
}
@media only screen and (min-width: 993px) {
  .container {
    width: 80%;
  }
}

* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(0deg, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.5)), url(./data/images/michal-parzuchowski-mountain.jpg); */
  background-size: cover;
  background-color:rgba(96, 125, 139, 0.6);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


